.pricing-list-container h4 {
  color: #66676b;
  background-color: #fff;
  border-bottom: 1px solid #66676b;
  width: 100%;
  padding: 30px;
  border-radius: 4px 4px 0 0;
  position: relative;
  z-index: 10;
  margin: 5px 0 0 0;
  display: block;
  font-weight: 500;
}
.pricing-list-container h4:after {
  width: 100%;
  height: 100%;
  border-radius: 4px 4px 0 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-color: #66676b;
  opacity: 0.05;
  z-index: 9;
}
.pricing-list-container ul {
  list-style: none;
  padding: 0;
  margin-bottom: 35px;
}
.pricing-list-container ul:last-child {
  margin-bottom: 25px;
}
.pricing-list-container ul li {
  padding: 15px 0;
  padding-right: 70px;
  position: relative;
}
.pricing-list-container ul li:last-child {
  border-radius: 0 0 4px 4px;
}
.pricing-list-container p {
  padding: 0;
  font-size: 15px;
  margin: 0;
  display: inline-block;
  width: 100%;
}
.pricing-list-container h5 {
  padding: 0;
  font-size: 15px;
  margin: 0;
  display: inline-block;
  width: 100%;
  font-size: 18px;
  margin-bottom: 1px;
}
.pricing-list-container p {
  color: #777;
  line-height: 24px;
  margin-top: 3px;
}
.pricing-list-container span {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  font-size: 18px;
  color: #777;
}

/* ---------------------------------- */
/* Blog Styles
------------------------------------- */
.blog-post {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  display: inline-block;
  width: 100%;
  border-radius: 3px;
  margin: 15px 0;
}

.post-img {
  position: relative;
  height: 100%;
  width: 100%;
  display: block;
}
.post-img img {
  width: 100%;
  border-radius: 3px 3px 0 0;
}

/* Post Meta */
.post-meta {
  list-style: none;
  padding: 0;
  margin: 5px 0 0 0;
}
.post-meta li {
  display: inline-block;
  color: #888;
}
.post-meta li:after {
  content: "";
  width: 1px;
  height: 12px;
  background: #d0d0d0;
  display: inline-block;
  position: relative;
  top: 0px;
  margin: 0 10px;
}
.post-meta li:last-child:after {
  display: none;
}
.post-meta li a {
  color: #888;
}
.post-meta li a:hover {
  color: #66676b;
}

/* Search Widget */
.search-blog-input {
  position: relative;
}

.widget .search-blog-input .input:before {
  content: "";
  font-size: 17px;
  color: #a0a0a0;
  position: absolute;
  display: inline-block;
  font-family: "simple-line-icons";
  right: 18px;
  top: 13px;
  margin-bottom: 0;
  background-color: #fff;
  padding-left: 7px;
}

/* Blog Widget Tabs */
.widget-tabs {
  padding: 0;
  list-style: none;
}
.widget-tabs li {
  padding: 0 0 27px 0;
  margin: 24px 0 0 0;
  border-bottom: 1px solid #e0e0e0;
}

.widget .tab-content {
  padding-bottom: 0;
}

.widget-thumb {
  width: 85px;
  margin-right: 23px;
}
.widget-thumb a img {
  display: block;
  width: 95px;
  height: 95px;
  float: left;
  border-radius: 3px;
  margin: 3px 22px 0 0;
}

.widget-text h5 {
  font-size: 15px;
  line-height: 24px;
  margin: 0 0 5px 0;
}
.widget-text h5 a {
  color: #333;
}
.widget-text h5 a:hover {
  color: #66676b;
}
.widget-text span {
  color: #888;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
  display: block;
}

.widget-thumb {
  display: inline-block;
}
.widget-thumb a img {
  width: 85px;
  height: auto;
}

.widget-text {
  display: inline-block;
  width: 70%;
  padding: 0;
  position: relative;
  top: -2px;
}

.widget-tabs .widget-content {
  display: table;
}
.widget-tabs .widget-content .widget-text {
  display: table-cell;
  vertical-align: middle;
}
.widget-tabs li {
  padding: 0 0 20px 0;
  margin: 24px 0 0 0;
}
.widget-tabs li:first-child {
  padding: 0 0 20px 0;
  margin: 32px 0 0 0;
}
.widget-tabs li:last-child {
  border: none;
  padding-bottom: 0;
}

.widget h3 {
  margin-top: 0;
  margin-bottom: 25px;
  font-size: 22px;
}

/* Single Post */
.single-post p {
  margin: 20px 0;
}
.single-post p:last-child {
  margin-bottom: 0px;
}
.single-post .post-img {
  border-radius: 3px 3px 0 0;
}

.blog-page .blog-post .post-content, .single-post .post-content {
  padding: 36px 43px;
}

/* About Author
------------------------------------- */
.about-author {
  margin: 10px 0 45px 0;
  padding: 30px 35px;
  background: #f8f8f8;
  border-radius: 4px;
}
.about-author p {
  margin: 5px 0 5px 0;
}
.about-author a {
  position: relative;
  top: -7px;
  color: #66676b;
}
.about-author h4 {
  letter-spacing: 0;
}
.about-author img {
  float: left;
  margin: 7px 5px 5px 5px;
  width: 110px;
  height: 110px;
  border-radius: 50%;
}

.about-description {
  margin: 2px 0 0 150px;
}

.blog-post-tags {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 0;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  opacity: 0.9;
}
.blog-post-tags li {
  display: inline-block;
  margin-right: 5px;
}
.blog-post-tags li i {
  margin-right: 3px;
  font-weight: normal;
  display: inline-block;
}

/* ---------------------------------- */
/* Coming Soon Page
------------------------------------- */
.coming-soon-page {
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  position: relative;
}
.coming-soon-page .container {
  z-index: 15;
  position: relative;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}
.coming-soon-page:before {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
  content: "";
  background-color: rgba(26, 26, 26, 0.5);
}
.coming-soon-page h2, .coming-soon-page h3 {
  margin: 0;
  padding: 0;
}
.coming-soon-page h2 {
  font-size: 46px;
  color: #fff;
}
.coming-soon-page h3 {
  font-size: 34px;
  font-weight: 300;
  margin: 30px 0 30px 0;
  color: rgba(255, 255, 255, 0.9);
}
.coming-soon-page .main-search-input-item input {
  font-size: 16px;
  font-weight: 600;
}
.coming-soon-page .gray-style.main-search-input {
  max-width: 650px;
  margin: 0 auto;
}
.coming-soon-page .main-search-input button.button {
  font-size: 16px;
}

@media (max-width: 992px) {
  .coming-soon-page {
    min-height: 100vh;
    height: auto;
  }
  .coming-soon-page .container {
    padding-top: 50px;
    padding-bottom: 50px;
    top: auto;
    transform: translateY(0%);
  }
}
/* Countdown */
#countdown {
  font-weight: 600;
  float: none;
  display: inline-block;
}
#countdown div {
  display: inline-block;
  margin: 0 5px;
  float: left;
}
#countdown div:first-child i {
  border: none;
}
#countdown span {
  background-color: rgba(255, 255, 255, 0.1);
  font-size: 40px;
  color: #fff;
  width: 120px;
  display: inline-block;
  padding: 32px 0;
  font-weight: 400;
  letter-spacing: -1px;
  border-radius: 3px 3px 0 0;
}
#countdown i {
  display: block;
  font-style: normal;
  margin: 0;
  text-transform: uppercase;
  font-size: 13px;
  padding: 8px 0;
  letter-spacing: 1px;
  color: #fff;
  border-radius: 0 0 3px 3px;
}

.countdown-text {
  font-size: 24px;
  margin-top: 15px;
  line-height: 36px;
  font-weight: 300;
}

@media (max-width: 550px) {
  #countdown {
    width: 100%;
  }
  #countdown div, #countdown span {
    width: 100%;
  }
  #countdown div {
    padding: 10px 0;
  }
}
.pricing-listing li {
  display: block;
  padding: 0.5rem 0;
}

.invoice-box {
  max-width: 800px;
  margin: auto;
  padding: 30px;
  border: 1px solid #eee;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  font-size: 16px;
  line-height: 24px;
  font-family: "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
  color: #555;
}
.invoice-box table {
  width: 100%;
  line-height: inherit;
  text-align: left;
}
.invoice-box table td {
  padding: 5px;
  vertical-align: top;
}
.invoice-box table tr td:nth-child(2) {
  text-align: right;
}
.invoice-box table tr.top table td {
  padding-bottom: 20px;
}
.invoice-box table tr.top table td.title {
  font-size: 45px;
  line-height: 45px;
  color: #333;
}
.invoice-box table tr.information table td {
  padding-bottom: 40px;
}
.invoice-box table tr.heading td {
  background: #eee;
  border-bottom: 1px solid #ddd;
