.ui-accordion .ui-accordion-header i,
.trigger a i {
  font-size: 18px;
  top: 1px;
  position: relative;
  padding-right: 3px;
}

.style-2 .ui-accordion .ui-accordion-header i,
.style-2 .trigger a i {
  padding-right: 6px;
