/* Style 2 of accordions and toggles */
.style-2 .ui-accordion .ui-accordion-header,
.style-2 .trigger a {
  border: none;
  margin: 0;
  padding: 27px 0;
  text-transform: none;
  font-size: 17px;
  font-weight: 500;
  background-color: transparent;
  color: #333;
  padding-right: 60px;
  font-weight: 400;
