.edge-bg {
  position: absolute;
  width: 50%;
  top: 0;
  right: 0px;
  height: 100%;
  background-size: 100%;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.edge-bg:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(34, 34, 34, 0.1);
}

/* Left-photo version */
.image-edge.left .image-edge-content {
  left: 50%;
}

.image-edge.left .edge-bg {
  left: 0px;
}

.image-edge.left .container {
  transform: translateX(54%);
  -webkit-transform: translateX(54%);
}

/* Styles for dark version  */
.image-edge.dark {
  background-color: #282828;
  color: #fff;
}

.image-edge.dark h1,
.image-edge.dark h2,
.image-edge.dark h3,
.image-edge.dark h4,
.image-edge.dark h5 {
  color: #fff;
}

.presentation-content h2,
.image-edge h2 {
  line-height: 56px;
  font-weight: 600;
  font-size: 40px;
  letter-spacing: -0.5px;
}

.presentation-content p,
.image-edge p {
  font-size: 20px;
  color: #828282;
  line-height: 34px;
}

.image-edge.dark p {
  color: rgba(255, 255, 255, 0.8);
}

/* List */
.image-edge-content ul {
  list-style: none;
  padding: 0;
}

.image-edge-content ul li {
  margin-bottom: 30px;
}

.image-edge-content ul li:last-child {
  margin-bottom: 0px;
}

.image-edge-content h4 i {
  position: relative;
  top: -1px;
  margin-right: 2px;
}

@media (max-width: 767px) {
  .edge-bg {
    position: relative;
    height: 400px;
    width: 100%;
    display: block;
    left: 0;
    right: 0;
  }
}
/*----------------------------------- */
/* Flip Banner
------------------------------------- */
.flip-banner {
  display: block;
  width: 100%;
  height: 180px;
  position: relative;
  text-align: center;
  overflow: hidden;
  z-index: 9999;
}

.flip-banner:after {
  background-color: #000;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  display: block;
  z-index: 100;
  transition: all 0.6s;
}

.flip-banner:hover:after {
  opacity: 0.6;
}

.flip-banner-content {
  position: relative;
  top: 50%;
  z-index: 200;
}

.flip-banner h2 {
  font-size: 32px;
  line-height: 40px;
  position: relative;
  width: 100%;
  display: block;
}

.flip-banner-content .flip-visible,
.flip-banner-content .flip-hidden {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: 0 auto;
  transform: translateY(-50%);
  transition: all 0.45s cubic-bezier(0.87, -0.41, 0.19, 1.44);
}

.flip-banner:hover .flip-banner-content .flip-hidden,
.flip-banner-content .flip-visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(-50%);
}

.flip-banner:hover .flip-banner-content .flip-visible,
.flip-banner-content .flip-hidden {
  opacity: 0;
  visibility: hidden;
  transform: translateY(150%);
}

.flip-banner:hover .flip-banner-content .flip-visible {
  transform: translateY(-150%);
}

.flip-banner i {
  padding: 0 10px;
}

.flip-banner .sl {
  position: relative;
  top: 1px;
  font-size: 26px;
}

@media (max-width: 767px) {
  .flip-banner .sl {
    font-size: 18px;
  }

  .flip-banner h2 {
    font-size: 22px;
    line-height: 34px;
  }
}
