/* Social Icons
------------------------------------- */
.social-icons {
  padding-left: 5px;
}

.social-icons li {
  display: inline;
  float: left;
  text-align: left;
  margin-right: 16px;
  margin-top: 11px;
  transform: scale(1.19);
  -webkit-transform: scale(1.19);
  -webkit-filter: blur(radius);
}

.social-icons li a {
  width: 44px;
  height: 44px;
