/* Tips */
.tip {
  font-style: normal;
  position: relative;
  top: -1px;
  left: 2px;
  display: inline-block;
  cursor: help;
  color: #fff;
  font-size: 11px;
  line-height: 0;
  width: 18px;
  height: 18px;
  background: #66676b;
  display: inline-block;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  padding: 0 !important;
  font-weight: 500;
}

.tip-content {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, calc(-100% - 20px));
  -ms-transform: translate(-50%, -100%);
  background: #333;
  color: #fff;
  padding: 13px 15px;
  min-width: 200px;
  max-width: 200px;
  font-size: 13px;
  border-radius: 4px;
  line-height: 19px;
  pointer-events: none;
  z-index: 999;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}

.tip:hover .tip-content {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, calc(-100% - 13px));
  -ms-transform: translate(-50%, -115%);
}
.tip:after {
  font-family: "FontAwesome";
  content: "";
  line-height: 19px;
  display: inline-block;
  position: relative;
  text-align: center;
  -moz-transform: translateY(-1px);
}

.tip-content:after {
  font-family: "FontAwesome";
  content: "";
  position: absolute;
  bottom: 3px;
  left: 50%;
  font-size: 22px;
  color: #333;
  transform: translate(-50%, 50%);
}

