  flex: 0 0 calc(25% - 25px);
}

/* 24 hour clock type */
.availability-slots.twenty-four-clock i.am-pm, .availability-slots.twenty-four-clock select.twelve-hr {
  display: none;
}

/* Slot Headline */
.day-slot-headline {
  background-color: #66676b;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  padding: 7px 10px;
  position: relative;
  cursor: default;
  font-weight: 600;
}

/* No Slots */
.no-slots {
  background-color: #f0f0f0;
  border-radius: 4px;
  margin-top: 8px;
  padding: 10px;
  display: flex;
  cursor: default;
  text-align: center;
  width: 100%;
  display: block;
  font-size: 14px;
  font-weight: 600;
  transition: 0.4s;
  opacity: 1;
  max-height: 60px;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 8px;
  opacity: 0;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 0;
}

.no-slots-fadeout {
  opacity: 0;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 0;
}

.no-slots.no-slots-fadein {
  opacity: 1;
  max-height: 60px;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 8px;
  display: block;
}

/* Single Slot */
.single-slot {
  background-color: #f3f3f3;
  border-radius: 4px;
  margin-top: 8px;
  padding: 0;
  display: flex;
  cursor: move;
}
.single-slot.slot-animation {
  animation-name: slotFadeIn;
  animation-duration: 0.3s;
}
.single-slot.cloned {
  display: none;
}

@keyframes slotFadeIn {
  0% {
    opacity: 0;
    max-height: 0;
  }
  100% {
    opacity: 1;
    max-height: 150px;
  }
}
.single-slot-left {
  flex: 1;
  padding: 12px 17px;
}

.single-slot-right {
  flex: 0 0 auto;
  background-color: #eee;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 4px 4px 0;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 17px;
}
