/* Plus Minus Buttons */
.plusminus {
  display: inline-block;
  position: relative;
  font-size: 0;
  overflow: hidden;
  border-radius: 0;
  width: 44px;
  height: 72px;
}
.plusminus button {
  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  margin: 0;
  padding: 0;
  border: 0;
  background: #fff;
  color: #aaa;
  font-size: 10px;
  font-weight: 500;
  line-height: 1;
  font-family: FontAwesome;
  cursor: pointer;
  transition: 0.3s;
}
.plusminus button:focus {
  outline: none;
}
.plusminus button:hover {
  background-color: #333;
  color: #fff;
}
.plusminus button:disabled:hover {
  background: #bbb;
  color: #fff;
  cursor: default;
}
.plusminus button:first-child {
  bottom: 0;
}
.plusminus button:first-child:before {
  content: "";
  line-height: 25px;
}
.plusminus button:last-child {
  top: 0;
}
.plusminus button:last-child:before {
  content: "";
  line-height: 25px;
}
.plusminus input[type=number] {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 24px;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 13px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
}
.plusminus input[type=number]::-webkit-outer-spin-button, .plusminus input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.plusminus.horiz {
  width: 74px;
  height: 24px;
}
.plusminus.horiz button:first-child {
  left: 0;
  border-radius: 3px;
}
.plusminus.horiz button:last-child {
  right: 0;
  border-radius: 3px;
  margin-left: 1px;
}
.plusminus.horiz:after {
  bottom: 0;
}
.plusminus.horiz input[type=number] {
  position: absolute;
  top: 0;
  left: 25px;
  border-radius: 3px;
}

