}

.map-box h4 a {
  padding: 0 0 2px 0;
  font-size: 17px;
  line-height: 25px;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  display: inline-block;
}

.listing-img-container {
  position: relative;
  height: 100%;
  display: block;
}

.map-box h4 a:hover {
  color: #66676b;
}

.map-box p {
  padding: 0;
  line-height: 25px;
  margin: 2px 0 0 0;
  font-size: 14px;
}

.map-box ul.listing-details {
  padding: 0;
  font-size: 14px;
  display: none;
}

.map-box .listing-title {
  padding: 16px 22px;
}

.map-box .listing-img-content {
  padding: 18px 22px;
}

.map-box .listing-img-content span {
  font-size: 21px;
}

.map-box .listing-img-content .listing-price i {
  font-size: 15px;
  margin: -7px 0 0 0;
}

.infoBox > img {
  position: absolute !important;
  right: 0px;
  top: 0px;
}

.map-box .listing-img-container::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  display: block;
  background: linear-gradient(to top, rgba(35, 35, 37, 0.85) 0%, rgba(35, 35, 37, 0.4) 35%, rgba(22, 22, 23, 0) 60%, rgba(0, 0, 0, 0) 100%);
  background-color: rgba(35, 35, 37, 0.2);
}

.map-box .listing-item-content {
  position: absolute;
  bottom: 20px;
  left: 0;
  padding: 0 24px;
  width: 100%;
  z-index: 50;
  box-sizing: border-box;
  background: transparent;
}

.map-box .listing-item-content h3 {
  color: #fff;
  font-size: 18px;
  padding: 0 0 2px 0;
  font-weight: 500;
  margin: 0;
  line-height: 27px;
}

.map-box .listing-item-content span {
  font-size: 15px;
  font-weight: 300;
  display: inline-block;
  color: rgba(255, 255, 255, 0.7);
}

/* Close Button */
.infoBox-close {
  position: absolute;
  top: 10px;
  right: 10px;
  display: inline-block;
  z-index: 999;
  text-align: center;
  line-height: 29px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  height: 29px;
  width: 29px;
  background-color: #fff;
  color: #333;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  font-family: "FontAwesome";
  border-radius: 50%;
  -webkit-text-stroke: 1px #fff;
}

.infoBox-close:hover {
  color: #fff;
  background-color: #66676b;
  -webkit-text-stroke: 1px #66676b;
}

/* Read More Icon */
.map-box-icon {
  height: 38px;
  width: 38px;
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #3685cf;
  display: block;
  color: #fff;
  font-size: 12px;
  z-index: 101;
  text-align: center;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -webkit-transform: translateX(50%);
  -ms-transform: translateX(50%);
  transform: translateX(50%);
  opacity: 0;
  visibility: hidden;
}

.map-box-icon:before {
  content: "";
  font-family: "FontAwesome";
  position: relative;
  top: 7px;
  display: block;
}

.map-box-image:hover .map-box-icon {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateX(0);
}

.map-box-image {
  position: relative;
  overflow: hidden;
  display: block;
}

.infoBox {
  -webkit-animation: fadeIn 0.9s;
  animation: fadeIn 0.9s;
}

/* Chrome, Safari, Opera */
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Standard syntax */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
