------------------------------------- */
.custom-zoom-in,
.custom-zoom-out {
  background-color: #fff;
  color: #333;
  cursor: pointer;
  border-radius: 50%;
  margin: 5px 15px;
  transition: color 0.3s, background-color 0.3s;
  box-shadow: 0px 1px 4px -1px rgba(0, 0, 0, 0.2);
  text-align: center;
  font-size: 15px;
  height: 34px;
  width: 34px;
}

.custom-zoom-in:hover,
.custom-zoom-out:hover {
  background-color: #66676b;
  color: #fff;
}

.custom-zoom-in:before,
.custom-zoom-out:before {
  font-family: "FontAwesome";
  width: 100%;
  line-height: 35px;
  transition: -webkit-text-stroke 0.3s;
  -webkit-text-stroke: 1px #fff;
}

.zoomControlWrapper {
  position: absolute;
  left: 0;
  right: auto;
  width: 70px;
}

.custom-zoom-in:before {
  content: "";
}

.custom-zoom-out:before {
  content: "";
}

.custom-zoom-in:hover:before,
.custom-zoom-out:hover:before {
  -webkit-text-stroke: 1px #66676b;
}

/* ---------------------------------- */
/* Parallax Background
------------------------------------- */
.parallax {
  background-image: url("../../../assets/images/slider-parallax.jpg");
  background-repeat: no-repeat;
  background-position: 50% 24%;
  position: relative;
  z-index: 99;
  background-attachment: fixed;
}
.parallax:after {
  background: rgba(0, 0, 0, 0.6);
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/* fullscreen setup */
.fullscreen,
.content-a {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.fullscreen.overflow,
.fullscreen.overflow .content-a {
  height: auto;
  min-height: 100%;
}

/* content centering styles */
.centered-content {
  position: relative;
  vertical-align: middle;
  text-align: center;
}

/* Parallax Overlay */
.parallax-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 101;
  background-color: #333;
  opacity: 0.4;
}

/* Parallax Content */
.parallax-content {
  position: relative;
  z-index: 999;
  padding: 105px 0;
}

body {
  overflow-x: hidden;
}

